import React from "react";
import styles from  "./App.module.scss";
import { Route, NavLink, HashRouter } from "react-router-dom";
import { BalancePage } from "./Pages/BalancePage";
import { StatsPage } from "./Pages/StatsPage";
import Visibility from "visibilityjs";

let selectorindex = 0;

export const App = (props) => {
   const [api, setApi] = React.useState({
      instances: [],
      balance: false,
      closed: false,
      open: false,
      profit: false
   });

   React.useEffect(() => {
      UpdateUI();
      let update = Visibility.every(1000, () => UpdateUI());

      return () => Visibility.stop(update);
	}, []);

   let UpdateUI = async () => {
      try {
         let req = await fetch(`/api/all?instance=${selectorindex}`);
         let res = await req.json();

         if (res) {
            setApi({
               instances: res.instances,
               balance: res.balance,
               closed: res.closed,
               open: res.open,
               profit: res.profit
            });
         }
      }catch(err) {
         console.log(err);
      }
   };

   let onIndexSelectChange = (event) => {
      selectorindex = event.target.value;
      UpdateUI();
   };

   return (
      <HashRouter>
         <div className={styles.outer_container}>
            <div className="menu">
               <NavLink className="menuitem wallet" exact to="/"/>
               <NavLink className="menuitem stats" to="/stats"/>
            </div>
            <div className={styles.page_container}>
               <div style={{
                  display: "grid",
                  gridTemplateRows: "auto auto auto",
                  gridTemplateColumns: "auto",
                  gridTemplateAreas: "'header' 'instance' 'content'"
               }}>
                  <div className="instance">
                     <div className="icon"/>
                     <select onChange={onIndexSelectChange}>
                        {api.instances.map((elem, index) => {
                           return (
                              <option key={index} value={index}>{elem}</option>
                           )
                        })}
                     </select>
                  </div>
                  <Route exact path="/">
                     <span className="header">DASHBOARD</span>
                     <BalancePage api={api}/>
                  </Route>
                  <Route exact path="/stats">
                     <span className="header">STATISTICS</span>
                  </Route>
               </div>
            </div>
         </div>
      </HashRouter>
   );
};