export default class Globals {
   static Round(num, precision) {
      return Math.round((num + Number.EPSILON) * Math.pow(10, precision)) / Math.pow(10, precision);
   }

   static GetPosNegColor(val, pos, neg, prim) {
      return val > 0 ? pos : val < 0 ? neg : prim;
   }

   static GetPartialGradient(rgb1, rgb2, pct) {
      return {
         r: Math.round(rgb1.r + (rgb2.r - rgb1.r) * pct),
         g: Math.round(rgb1.g + (rgb2.g - rgb1.g) * pct),
         b: Math.round(rgb1.b + (rgb2.b - rgb1.b) * pct),
      }
   }

   static clone = obj => JSON.parse(JSON.stringify(obj));
}