import React from "react";

export const Panel = (props) => {
   return (
      <div style={Object.assign({
         display: "flex",
         flexDirection: "column",
         backgroundColor: "#191919",
         border: "1px solid #2a2a2a",
         borderRadius: 15,
         padding: 10,
         alignItems: "center"
      }, props.style)}>
         {props.children}
      </div>
   );
};