import React from "react";
import { Panel } from "./Panel";
import { LineChart, Line, CartesianGrid, YAxis, XAxis, Tooltip } from "recharts";
import Globals from "./Globals";
import styles from "./TradesPanel.module.scss";

const months = ["Jan", "Feb", "March", "Apr", "May", "June", "July", "Aug", "Sep", "Oct", "Nov", "Dec"];

function CreateTrade(elem) {
   let timesince = Date.now() - (elem.close_timestamp ?? elem.open_timestamp);
   let sec = Math.floor(timesince / 1000);
   let min = Math.floor(sec / 60);
   let hr = Math.floor(min / 60);
   let day = Math.floor(hr / 24);
   let week = Math.floor(day / 7);
   let month = Math.floor(day / (365 / 12));
   let year = Math.floor(day / 365);

   let formatted = "";
   if (year >= 1)
      formatted = `${year}y`;
   else if (month >= 1)
      formatted = `${month}mo`;
   else if (week >= 1)
      formatted = `${week}w`;
   else if (day >= 1)
      formatted = `${day}d`;
   else if (hr >= 1)
      formatted = `${hr}h`;
   else if (min >= 1)
      formatted = `${min}m`;
   else if (sec >= 1)
      formatted = `${sec}s`;

   return {
      trade_id: elem.trade_id,
      pair: elem.pair,
      open_ts: elem.open_timestamp,
      close_ts: elem.close_timestamp ?? Date.now(),
      amount: elem.amount,
      open_rate: elem.open_rate,
      close_rate: elem.current_rate,
      profit_pct: elem.profit_pct,
      profit_abs: elem.profit_abs,
      is_open: elem.is_open,
      time_formatted: formatted
   };
}

export const TradesPanel = (props) => {
   const {balance, closed, open, profit} = props.api;

   let balanceData = [];
   let tradesData = [];
   let balanceStake = "";
   if (balance && closed && open && profit) {
      let bal = balance.total - profit.profit_all_coin;
      let _yAxisDomain = [bal, bal];
      closed.sort(function(a, b) {
         if (a.close_timestamp > b.close_timestamp) return 1;
         if (a.close_timestamp < b.close_timestamp) return -1;
         return 0;
      });
      open.sort(function(a, b) {
         if (a.open_timestamp < b.open_timestamp) return 1;
         if (a.open_timestamp > b.open_timestamp) return -1;
         return 0;
      });
      
      balanceData.push({
         x: closed[0].open_timestamp,
         y: bal
      });

      closed.forEach((elem, index) => {
         bal += elem.profit_abs;
         balanceData.push({
            x: elem.close_timestamp,
            y: bal
         });
         if (bal < _yAxisDomain[0]) _yAxisDomain[0] = bal;
         if (bal > _yAxisDomain[1]) _yAxisDomain[1] = bal;
      });

      if (open.length > 0) {
         open.forEach(elem => {
            bal += elem.profit_abs;
         });
         balanceData.push({
            x: Date.now(),
            y: bal
         });
         if (bal < _yAxisDomain[0]) _yAxisDomain[0] = bal;
         if (bal > _yAxisDomain[1]) _yAxisDomain[1] = bal;
      }

      open.forEach(elem => tradesData.push(CreateTrade(elem)));
      closed.reverse().forEach(elem => tradesData.push(CreateTrade(elem)));

      _yAxisDomain[0] = Math.ceil(_yAxisDomain[0]);
      _yAxisDomain[1] = Math.ceil(_yAxisDomain[1]);

      balanceStake = balance.stake;
   }

   return (
      <Panel style={props.style}>
         <div className={styles.graph}>
            <LineChart width={578} height={250} data={balanceData} margin={{ top: 5, right: 20, bottom: 5, left: 0 }}>
               <CartesianGrid stroke="#2a2a2a"/>
               <Line
                  type="monotone"
                  dataKey="y"
                  stroke="#42c2f4"
                  strokeWidth={2}
                  isAnimationActive={false}
                  dot={false}/>
               <YAxis
                  orientation="right"
                  tickCount={10}
                  domain={["dataMin", "dataMax"]}
                  scale="linear"
                  allowDecimals={false}/>
               <XAxis
                  dataKey="x"
                  scale="time"
                  tickFormatter={(value) => `${new Date(value).getDate()} ${months[new Date(value).getMonth()]}`}/>
               <Tooltip
                  contentStyle={{backgroundColor: "#0f0", borderColor: "#2f2f2f"}}
                  allowEscapeViewBox={{x: true, y: true}}
                  formatter={(value, name, props) => [value.toFixed(2), "Balance"]}
                  labelFormatter={(value) => `${new Date(value).getDate()} ${months[new Date(value).getMonth()]}`}/>
            </LineChart>
         </div>
         <div className={styles.list_header}>
            <span>Pair</span>
            <span>Amount</span>
            <span>Profit (pct)</span>
            <span>Profit ({balanceStake})</span>
            <span>Time</span>
         </div>
         <div className={styles.list_container}>
            {tradesData.map(elem => {
               return (
                  <div className="entry" key={elem.trade_id}>
                     <span style={{color: elem.is_open ? "#3d3" : ""}}>{elem.pair}</span>
                     <span>{Globals.Round(elem.amount, 2)}</span>
                     <span>{elem.profit_pct > 0 ? "+" : ""}{elem.profit_pct.toFixed(2)}%</span>
                     <span>{elem.profit_abs > 0 ? "+" : ""}{elem.profit_abs.toFixed(2)}</span>
                     <span>{elem.time_formatted}</span>
                  </div>
               )
            })}
         </div>
      </Panel>
   );
};