import React from "react";
import { PieChart } from "react-minimal-pie-chart";
import { Panel } from "./Panel";
import Globals from "./Globals";
import styles from "./BalancePanel.module.scss";

export const BalancePanel = (props) => {
   const {balance, closed, open, profit} = props.api;

   let pieData = [];
   let currencyData = [];
   let balanceTotal = 0;
   let balanceStake = "";
   if (balance && closed && open && profit) {
      balance.currencies.sort(function(a, b) {
         if (a.est_stake < b.est_stake) return 1;
         if (a.est_stake > b.est_stake) return -1;
         return 0;
      });

      let singular_count = balance.currencies.filter(elem => elem.est_stake > (balance.total * 0.01)).length;

      balance.currencies.forEach((elem, index) => {
         if (index < singular_count) {
            let pct = index / singular_count;
            let color = Globals.GetPartialGradient({r: 66, g: 194, b: 244}, {r: 134, g: 65, b: 244}, pct);
            pieData.push({
               value: elem.est_stake,
               color: `rgb(${color.r}, ${color.g}, ${color.b})`
            });
         }else{
            if (index === singular_count) {
               pieData.push({
                  value: elem.est_stake,
                  color: "#EAECEF"
               });
            }else{
               pieData[singular_count - 1].value += elem.est_stake;
            }
         }
      });
      currencyData = balance.currencies;
      balanceTotal = balance.total;
      balanceStake = balance.stake;
   }

   return (
      <Panel style={props.style}>
         <div className={styles.pie_container}>
            <PieChart
               startAngle={-90}
               lineWidth={20}
               data={pieData}/>
            <div className="center_info_container">
               <span className="secondary">Total balance</span>
               <span style={{fontSize: 40, lineHeight: 1}}>{balanceTotal.toFixed(2)}</span>
               <span className="secondary">{balanceStake}</span>
            </div>
         </div>
         <div className={styles.list_container}>
            {currencyData.map((elem, index) => {
               return (
                  <div className="entry" key={elem.currency}>
                     <div className="color" style={{backgroundColor: index < pieData.length ? pieData[index].color : "#EAECEF"}}/>
                     <div style={{display: "flex", justifyContent: "space-between", flexGrow: 1}}>
                        <span>{elem.currency}</span>
                        <span>{Globals.Round(elem.balance, 4)}</span>
                     </div>
                  </div>
               )
            })}
         </div>
      </Panel>
   );
};