import React from "react";
import { BalancePanel } from "../Components/BalancePanel";
import { TradesPanel } from "../Components/TradesPanel";
import Globals from "../Components/Globals";

export const BalancePage = (props) => {
   return (
      <div style={{
         display: "grid",
         gridTemplateColumns: "350px 600px",
         gridTemplateRows: "600px",
         columnGap: 30,
         gridTemplateAreas: "'bal trades'"
      }}>
         <BalancePanel style={{gridArea: "bal"}} api={Globals.clone(props.api)}/>
         <TradesPanel style={{gridArea: "trades"}} api={Globals.clone(props.api)}/>
      </div>
   )
};